.block-left-container {
  flex: auto;
  width: 100%;
  height: var(--dl-size-size-xxlarge);
  display: flex;
  overflow: hidden;
  position: relative;
  align-self: center;
  align-items: center;
  border-radius: 12px;
  background-color: var(--dl-color-gray-white);
}
.block-left-image {
  width: 400px;
  height: 288px;
  align-items: center;
}
.block-left-image1 {
  width: 400px;
  height: 288px;
  object-fit: cover;
}
.block-left-text {
  gap: var(--dl-space-space-unit);
  height: 100%;
  display: flex;
  padding: var(--dl-space-space-twounits);
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.block-left-tittle {
  color: rgb(34, 34, 35);
  width: 100%;
  font-size: 20px;
  font-style: normal;
  text-align: left;
  line-height: 1.2;
}
.block-left-content {
  gap: var(--dl-space-space-halfunit);
  display: flex;
  position: relative;
  align-items: flex-start;
  flex-direction: column;
}
.block-left-text1 {
  width: 100%;
}
.block-left-text2 {
  width: 100%;
}


@media(max-width: 1200px) {
  .block-left-tittle {
    line-height: 1.1;
  }
}
@media(max-width: 767px) {
  .block-left-container {
    height: auto;
    flex-direction: column-reverse;
  }
  .block-left-image {
    width: auto;
    position: relative;
  }
  .block-left-image1 {
    width: 703px;
  }
  .block-left-text {
    align-self: center;
    align-items: center;
    padding-top: var(--dl-space-space-threeunits);
  }
}
@media(max-width: 479px) {
  .block-left-tittle {
    text-align: center;
  }
  .block-left-text1 {
    text-align: center;
  }
  .block-left-text2 {
    text-align: center;
  }
}
