.block-right-container {
  flex: auto;
  width: 100%;
  height: var(--dl-size-size-xxlarge);
  display: flex;
  overflow: hidden;
  position: relative;
  align-self: center;
  align-items: center;
  border-radius: 12px;
  flex-direction: row;
  background-color: var(--dl-color-gray-white);
}
.block-right-text {
  gap: var(--dl-space-space-unit);
  height: 100%;
  display: flex;
  padding: var(--dl-space-space-twounits);
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.block-right-tittle {
  color: rgb(34, 34, 35);
  width: 100%;
  font-size: 20px;
  font-style: normal;
  text-align: left;
  line-height: 1.2;
}
.block-right-content {
  gap: var(--dl-space-space-halfunit);
  display: flex;
  position: relative;
  align-items: flex-start;
  flex-direction: column;
}
.block-right-text1 {
  width: 100%;
}
.block-right-text2 {
  width: 100%;
}
.block-right-image {
  width: 400px;
  height: 288px;
  overflow: visible;
  align-items: center;
}
.block-right-image1 {
  width: 400px;
  height: 288px;
  overflow: visible;
  object-fit: cover;
}


@media(max-width: 1200px) {
  .block-right-tittle {
    line-height: 1.1;
  }
}
@media(max-width: 767px) {
  .block-right-container {
    height: auto;
    flex-direction: column;
  }
  .block-right-text {
    align-self: center;
    align-items: center;
    padding-top: var(--dl-space-space-threeunits);
  }
  .block-right-image {
    width: auto;
    position: relative;
  }
  .block-right-image1 {
    width: 703px;
  }
}
@media(max-width: 479px) {
  .block-right-tittle {
    text-align: center;
  }
  .block-right-text1 {
    text-align: center;
  }
  .block-right-text2 {
    text-align: center;
  }
}
